.top-service {
  padding-bottom: 100px;
  opacity: 0;
  transform: translate(0, 150px);
  transition: all 1500ms;
}

@media screen and (max-width: 768px) {
  .top-service ._container {
    width: 100% !important;
  }
}

.top-service.services-scroll {
  opacity: 1;
  transform: translate(0, 0);
}

.top-service .sec-title__jp {
  border-bottom: 5px solid #ffe621;
}

.top-service_list {
  list-style: none;
}

.top-service_list_item {
  margin-top: 400px;
  transform: translate(0, 150px);
  transition: all 1500ms;
}

.top-service__list_top {
  display: flex;
  margin-bottom: 30px;
}

.top-service__list_p {
  line-height: 1.7;
}

.top-service__list_number {
  font-size: 2vw;
  padding: 16px 10px 10px 0;
  border-right: solid 2px #FFF;
}

@media screen and (max-width: 768px) {
  .top-service__list_number {
    font-size: 23px;
    padding: 13px 10px 10px 0;
  }
}

.top-service__list_text {
  margin-left: auto;
  color: #fff;
  padding: 40px;
  position: relative;
  background: rgba(12, 46, 90, 0.9);
  width: 50%;
}

@media screen and (max-width: 768px) {
  .top-service__list_text {
    width: 75%;
    padding: 20px;
  }
}

.top-service__list_title {
  padding-left: 10px;
}

.top-service__list_title .service_title_en {
  font-size: 1vw;
}

.top-service__list_title .service_title_ja {
  font-size: 2vw;
  padding-top: 10px;
}

@media screen and (max-width: 768px) {
  .top-service__list_title .service_title_ja {
    font-size: 23px !important;
  }
  .top-service__list_title .service_title_en {
    font-size: 14px !important;
  }
}

.top-service__list_web {
  margin-top: 190px;
}

.top-service__list_web .top-service__list_text::before {
  content: "";
  position: absolute;
  background: rgba(248, 82, 69, 0.1);
  width: 162%;
  height: 145%;
  top: -21%;
  left: -52%;
  z-index: -2;
}

.top-service__list_web .top-service__list_text::after {
  content: "";
  background-image: url("../img/web.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: right;
  position: absolute;
  width: 117%;
  height: 129%;
  top: -59%;
  left: -100%;
  z-index: -1;
}

.top-service__list_media .top-service__list_text::before {
  content: "";
  position: absolute;
  background: rgba(69, 176, 53, 0.1);
  width: 162%;
  height: 145%;
  top: -21%;
  left: -52%;
  z-index: -2;
}

.top-service__list_media .top-service__list_text::after {
  content: "";
  background-image: url("../img/media.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: right;
  position: absolute;
  width: 117%;
  height: 129%;
  top: -59%;
  left: -100%;
  z-index: -1;
}

.top-service__list_edu {
  margin-bottom: 190px;
}

.top-service__list_edu .top-service__list_text::before {
  content: "";
  position: absolute;
  background: rgba(255, 230, 33, 0.1);
  width: 162%;
  height: 145%;
  top: -21%;
  left: -52%;
  z-index: -2;
}

.top-service__list_edu .top-service__list_text::after {
  content: "";
  background-image: url("../img/lgbt.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: right;
  position: absolute;
  width: 117%;
  height: 129%;
  top: -59%;
  left: -100%;
  z-index: -1;
}

@media screen and (max-width: 768px) {
  .top-service__list_web .top-service__list_text::before,
  .top-service__list_media .top-service__list_text::before,
  .top-service__list_edu .top-service__list_text::before {
    width: 110%;
    height: 135%;
    top: -27%;
    left: -22%;
    z-index: -2;
  }
  .top-service__list_web .top-service__list_text::after,
  .top-service__list_media .top-service__list_text::after,
  .top-service__list_edu .top-service__list_text::after {
    width: 111%;
    height: 250px;
    top: -220px;
    left: 77%;
    transform: translateX(-100%);
    z-index: -1;
  }
}

.company_inner {
  justify-content: space-around;
  padding-bottom: 60px;
}

.company_inner table.company_table tbody tr {
  border-bottom: 2px solid #0c2e5a;
}

.company_inner table.company_table tbody tr td {
  line-height: 1.4;
  width: 200px;
  padding: 10px 0;
}

@media screen and (max-width: 768px) {
  .company_inner table.company_table {
    margin: 0 auto 30px;
  }
}

@media screen and (max-width: 768px) {
  .company_map {
    width: fit-content;
    margin: 0 auto;
  }
  .company_map iframe {
    width: 100%;
  }
}
